* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212122;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.form-wrapper {
  z-index: 5;
  height: 72px;
  background-color: #007aff;
  padding: 15px;
  display: inline;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.search-form {
  z-index: 10px;
  width: 100%;
  height: 40px;
  border-radius: 10px;
  justify-content: center;
  display: flex;
  position: fixed;
}

input, button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10px;
  outline: none;
  margin-left: -20px;
  padding: 10px 15px;
  font-size: 20px;
}

button {
  z-index: 6;
  background-color: #b5bddc;
  border-radius: 0 10px 10px 0;
  font-size: 20px;
}

button:hover, button:target {
  color: #fff;
  z-index: 10;
  background-color: #000;
  border-radius: 10px;
}

.photo-card {
  border: none;
  border-bottom: 1px solid rgba(27, 31, 35, .15);
  border-radius: 0 0 4px 4px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 1px rgba(27, 31, 35, .04), 0 1px rgba(255, 255, 255, .25);
}

.image {
  width: 350px;
  height: 200px;
  height: 30vh;
  object-fit: cover;
  display: block;
}

.load-more {
  width: 180px;
  height: 50px;
  color: #fafafa;
  cursor: pointer;
  z-index: 1000;
  opacity: 1;
  background-color: red;
  border: none;
  border-radius: 5px;
  margin-top: 25px;
  margin-left: 50%;
  display: block;
  position: fixed;
  bottom: 3vh;
}

.load-more:hover {
  opacity: .8;
  background-color: #007aff;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
}

.load-more.is-hidden {
  display: none;
}

.img_card {
  position: relative;
}

.image:hover, .image:focus {
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.photo-card {
  margin: 15px;
  display: flex;
}

.info {
  color: #000;
  flex-wrap: wrap;
  display: flex;
  bottom: 0;
  left: 0;
}

.info-item {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 20px;
}

.gallery {
  flex-flow: wrap;
  flex-basis: calc(33.3333% - 10px);
  justify-content: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  overflow: hidden;
}

